<template>
	<div id="shop-goods-list">
		<search-header>
				<div class="share" slot="icon">
					<img class="share-icon" src="../../assets/present_btn_share@2x.png">
				</div>
		</search-header>
		
		<div class="shop-top">
			<div class="shop-banner" v-if="shops.length > 0">
				<img :src="shops[0].bgImagePath" alt="" style="display: inline-block;">
				<div class="shop-footer">
					<div class="shop-logo">
						<img class="shop-logo-img" :src="shops[0].iconPath" alt="">
					</div>
					<div class="shop-text">
						<div class="shop-name">{{shops[0].orgName}}</div>
						<div class="shop-good">{{shops[0].description}}</div>
					</div>
				</div>
			</div>
		</div>
		<div class="shop-classify">
			<div class="common-good" :class="{ active:1 == classifyTab }" @click="changTab(1)">普通商品</div>
			<div class="order-good" :class="{ active:2 == classifyTab }" @click="changTab(2)">定制礼物</div>
		</div>

		<rang-tab v-if='classifyTab==1' v-on:sort='sort'></rang-tab>
		
		<div 
			v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="15"
      infinite-scroll-immediate-check="false">
			<good-list :goodList="goodList"></good-list>
		</div>
	</div>
</template>

<script>
	import SearchHeader from '@/components/Header/SearchHeader'
	import RangTab from '@/components/RangTab/RangTab'
	import GoodList from '@/components/ChooseGift/GoodList'
	
	
	export default {
		data() {
			return {
				classifyTab : 1,
				shops:[],
				goodList:[],
				PARAMS:{
					a:0,
					type:1,
					pa:1,
					li:10,
					ob:1,
					orgId:this.$route.query.orgId,
					spuId:'',
					promId:'',
					categoryId:'',
					searchKey:'',
					lastId:'',
				},
	      loading: false,
			}
		},
		
		created(){
			this.getShopList()
			this.paramsInit()
		},
		
		methods: {
			//排序
			sort(arg){
				this.PARAMS = Object.assign({},this.PARAMS,{
					pa:1,
					ob:arg,
					lastId:'',
				})
				this.getGoodsList()
			},
			
			changTab(arg){
				this.classifyTab = arg;
				this.PARAMS = Object.assign({},this.PARAMS,{
					pa:1,
					type:arg,
					ob:1,
					lastId:'',
				})
				this.getGoodsList()
			},
			paramsInit(){
//				console.log(this.$route.query.orgId)
				this.PARAMS = {
					a:this.$route.query.a?this.$route.query.a:'0',
					type:this.$route.query.type?this.$route.query.type:1,
					pa:1,
					li:10,
					ob:1,
					orgId:this.$route.query.orgId?this.$route.query.orgId:'0',
					spuId:this.$route.query.spuId?this.$route.query.spuId:'',
					promId:this.$route.query.promId?this.$route.query.promId:'',
					categoryId:this.$route.query.categoryId?this.$route.query.categoryId:'',
					searchKey:this.$route.query.searchKey?this.$route.query.searchKey:'',
					lastId:'',
				}
//				console.log(this.PARAMS)
				this.getGoodsList()
			},
			
			getGoodsList(){
				Object.keys(this.PARAMS).forEach((key) => (this.PARAMS[key]=='') && delete this.PARAMS[key]);
				this.$Api.getGoodsList(this.PARAMS).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						if(this.PARAMS.pa == 1){
							this.goodList = res.q.goodses
						}else if(this.PARAMS.pa > 1 && res.q.goodses.length > 0){
							let gl = this.goodList
							this.goodList = gl.concat(res.q.goodses)
						}
					}
				})
			},
			loadMore() {
      	this.loading  = true;
				setTimeout(()=>{
					this.PARAMS = Object.assign({},this.PARAMS,{
						pa:parseInt(this.PARAMS.pa)+1,
					})
		      this.getGoodsList();
		      this.loading  = false;
				},1000)
	    },
			getShopList(){
				let params={
					a:0,
					type:1,
					orgId:this.$route.query.orgId,
				}
				console.log(params)
				this.$Api.getShopList(params).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.shops = res.q.shops
					}
				})
			},
		},
		components: {
			SearchHeader,
			RangTab,
			GoodList,
		},

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#shop-goods-list {
		.shop-top{
			position: relative;
			/*padding-top:1.17rem;*/
			height:5.33rem;
			.shop-banner{
				position: relative;
				>img{
					width: 100%;
					height: 5.33rem;
				}
				.shop-footer{
					position: absolute;
					display: flex;
					bottom: 0;
					padding: 0 0.4rem;
					width: 100%;
					height: 1.6rem;
					background: #000;
					.shop-logo{
						position: relative;
    				bottom: 0.67rem;
						width: 1.97rem;
						height: 1.97rem;
						border-radius: 50%;
						border: 1px solid rgb(204, 204, 204);

					}
					.shop-logo-img{
						width: 1.97rem;
						height: 1.97rem;
						border-radius: 50%;
					}
					.shop-text{
						/*flex: 1;*/
						margin-left: 0.4rem;
						display: flex;
						flex-direction:column;
						justify-content:center;

					}
					.shop-name{
						font-size: 0.4rem;
						color: #fff;
					}
					.shop-good{
						font-size: 0.37rem;
						color: #fff;

					}
				}
			}
		}
		.shop-classify{
			display: flex;
			height: 1.07rem;
			line-height: 1.07rem;
			font-size: 0.4rem;
			border-bottom: 1px solid $border-color;
			>div{
				text-align: center;
				flex:1;
			}
			.active{
				color: $label-color;
				border-bottom: 0.05rem solid rgb(222, 127, 239);
			}
		}
		.order-list {
			display: flex;
			font-size: 0.4rem;
			flex-direction: row;
			border-bottom: 1px solid $border-color;
			height: 1.07rem;
			line-height: 1.07rem;
			align-items: center;
			.active {
				color: $label-color;
			}
			>div {
				position: relative;
				height: 0.67rem;
				line-height: 0.67rem;
				text-align: center;
				flex: 1;
				border-right: 1px solid $border-color;
				&:last-child {
					border-right: none;
				}
			}
			.rang-icon-no {
				&:after{
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right:0.53rem;
					top:51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-no.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
			}
			.rang-icon-up {
				&:after{
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right:0.53rem;
					top:51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-up.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
				
			}
			.rang-icon-down {
				&:after{
					content: '';
					height: 0.4rem;
					width: 0.2rem;
					display: block;
					position: absolute;
					right:0.53rem;
					top:51%;
					transform: translateY(-50%);
					background-image: url(../../assets/rang-icon-down.png);
					background-repeat: no-repeat;
					background-size: 0.2rem 0.4rem;
				}
				
			}
		}
	}
</style>


